import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'//引入vue-router
import router from './router'///引入路由器（自己创建的）完整路径是./router/index.js
import axios from 'axios'
import './assets/css/normalize.css'//全局样式
import './assets/css/page.css'//全局样式

//element-ui按需引入组件，cli会把对应样式会自动引进来
import {Button,Tabs,Input,TabPane,Form,FormItem,Upload} from 'element-ui';//{}内是组件名

//axios.defaults.baseURL="http://47.95.122.197:7001/";

Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.component(Button.name, Button);
Vue.component(Tabs.name, Tabs);
Vue.component(Input.name, Input);
Vue.component(TabPane.name, TabPane);
Vue.component(Form.name, Form);
Vue.component(FormItem.name, FormItem);
Vue.use(Upload);
Vue.prototype.$axios = axios


new Vue({
  render: h => h(App),
  router:router
}).$mount('#app')
