<script>
export default {
  name: "HotTopic",
  data() {
    return {
      src: [
        { img: require("../assets/热点榜单照片/国2.jpg") },
        { img: require("../assets/热点榜单照片/文1.jpg") },
        { img: require("../assets/热点榜单照片/科2.png") },
        { img: require("../assets/热点榜单照片/外1.jpg") },
        { img: require("../assets/热点榜单照片//国3.jpg") },
        { img: require("../assets/热点榜单照片/文2.jpg") },
      ],
      translateX: 0,
      tsion: true,
    };
  },
  methods: {
    last() {
      this.translateX--;
      this.tsion = true;
      if (this.translateX < 0) {
        setTimeout(() => {
          this.tsion = false;
          this.translateX = this.src.length - 1;
        }, 500);
      }
    },
    next() {
      this.translateX++;
      this.tsion = true;
      if (this.translateX > this.src.length - 1) {
        setTimeout(() => {
          this.tsion = false;
          this.translateX = 0;
        }, 500);
      }
    },
    swiper(i) {
      this.translateX = i;
    },
  },
  mounted() {
    setInterval(() => {
      this.next();
    }, 3000);
  },
  computed: {
    translate() {
      return -(this.translateX + 1) * 800;
    },
  },
  watch: {
    translateX: {
      handler(val) {
        let a = this.$refs.swiper.querySelectorAll("span");
        a.forEach((element) => {
          element.style.width = "12px";
        });
        if (this.translateX < 0) {
          val = this.src.length - 1;
        }
        if (this.translateX > this.src.length - 1) {
          val = 0;
        }
        a[val].style.width = "1000px";
      },
    },
  },
};
</script>

