//该文件专用于创建整个应用的路由器
import VueRouter from 'vue-router'
import router from '../router'
//引入组件
import AboutUs from '../pages/AboutUs.vue'
import WriTemplate from '../pages/WriTemplate.vue'
import HotTopic from '../pages/HotTopic.vue'
import AiWriter from '../pages/AiWriter.vue'
import LoginIn from '../pages/LoginIn.vue'
import WritingTemplate1 from '../components/WritingTemplate1.vue'
import WritingTemplate2 from '../components/WritingTemplate2.vue'
import WritingTemplate3 from '../components/WritingTemplate3.vue'
import WritingTemplate4 from '../components/WritingTemplate4.vue'
import WritingTemplate5 from '../components/WritingTemplate5.vue'
import WritingTemplate6 from '../components/WritingTemplate6.vue'
import WritingTemplate7 from '../components/WritingTemplate7.vue'
import WritingTemplate8 from '../components/WritingTemplate8.vue'
import WritingTemplate9 from '../components/WritingTemplate9.vue'
//创建并暴露一个路由器
export default new VueRouter({
	mode:'history',//默认是hash
	routes:[
		{
			path:'/',
			redirect:'/AboutUs'//重定向
		},
		{
			path:'/AboutUs',
			component:AboutUs,
		},
		{
			path:'/login',
			component:LoginIn,
		},
		{
			path:'/AiWriter',
			component:AiWriter,
		},
		{
			path:'/HotTopic',
			component:HotTopic,
		},

		{
			path:'/WriTemplate',
			component:WriTemplate,
		},
		{
			path:'/WriTemplate/tempa',
			component:WritingTemplate1,
		},
		{
			path:'/WriTemplate/tempb',
			component:WritingTemplate2,
		},
		{
			path:'/WriTemplate/tempc',
			component:WritingTemplate3,
		},
		{
			path:'/WriTemplate/tempd',
			component:WritingTemplate4,
		},
		{
			path:'/WriTemplate/tempe',
			component:WritingTemplate5,
		},
		{
			path:'/WriTemplate/tempf',
			component:WritingTemplate6,
		},
		{
			path:'/WriTemplate/tempg',
			component:WritingTemplate7,
		},
		{
			path:'/WriTemplate/temph',
			component:WritingTemplate8,
		},
		{
			path:'/WriTemplate/tempi',
			component:WritingTemplate9,
		},
	]
})

//全局前置路由守卫 初始化和跳转之前被调用
router.beforeEach((to,from,next)=>{
	// if(to.name!='login' && !token){//如果需要校验 去的不是登录页且没有token
	// 		next({name:login})//跳到登录页
	// }else{//去的是登录页 或者 有token
	//		next()	
	// }
	next()
})
