<template>
	<div>
		<div class="header"></div>
		<div class="content">
<!-- 			<div class="res1">
				<span><el-button type="primary">生成标题</el-button></span>
				<span><el-button type="primary">生成摘要</el-button></span><el-button type="primary">生成标题</el-button>
			</div> -->

			<div class="form-item">
				<p>
					请选择要上传的文件
				</p>
				<br />
				<input class="file" name="file" type="file" accept="image/png,image/gif,image/jpeg" @change="getFile"/>
			</div>
			<br />
			<br />
			<el-button type="primary" @click="submit">提交</el-button>
			<br />
			<br />
			<div class="res2">
				<p>{{title}}</p>
			</div>
			<div class="res3">
				<p>{{abstract}}</p>
			</div>
			<div class="pad"></div>
			<!-- <textarea></textarea> -->
		</div><!-- content -->
	</div><!-- root -->
</template>

<script scoped>
	import axios from 'axios'
	  export default {
	   name:'aiwriter',
	    data() {
	        return {
			  title: '生成标题',
			  abstract: '生成摘要',
	          file: '',
	        }
	      },
	      methods: {
			getFile(event) {
			this.file = event.target.files[0];
			console.log(this.file);
			},
			
			submit(event) {
				event.preventDefault();//取消默认行为
				//创建 formData 对象
				let formData = new FormData();
				// 向 formData 对象中添加文件
				formData.append('file',this.file);
				axios.post(
					'/upload/post',
					formData
				).then(res=>{
					console.log("res: ",res.data);
					this.title=res.data.title;
					this.abstract=res.data.abstract;
				})
			},
	       
	      }
	  }
</script>

