<template>
	<div class="container">
		<div class="login-pad"></div>
		<div class="login">
					<el-tabs v-model="activeName" @tab-click="handleClick">
						<el-tab-pane label="登录" name="first">
							<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
								<el-form-item label="名称" prop="name"><el-input v-model="ruleForm.name"></el-input></el-form-item>
			 
								<el-form-item label="密码" prop="pass"><el-input type="password" v-model="ruleForm.pass" auto-complete="off"></el-input></el-form-item>
			 
								<el-form-item>
									<el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
			 
									<el-button @click="resetForm('ruleForm')">重置</el-button>
								</el-form-item>
							</el-form>
						</el-tab-pane>
			 
						<el-tab-pane label="注册" name="second">
							<RegisterCom></RegisterCom>
						</el-tab-pane>
					</el-tabs>
		</div>
		<div class="login-pad"></div>
	</div>

</template>
 
<script>
import RegisterCom from './RegisterCom.vue';
 
export default {
	data() {
		var validatePass = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('please enter the password'));
			} else {
				if (this.ruleForm.checkPass !== '') {
					this.$refs.ruleForm.validateField('checkPass');
				}
 
				callback();
			}
		};
 
		return {
			activeName: 'first',
			ruleForm: {
				name: '',
				pass: '',
				checkPass: ''
			},
			rules: {
				name: [{ required: true, message: 'please enter your account', trigger: 'blur' }, { min: 3, max: 11, message: '长度在 3 到 11 个字符', trigger: 'blur' }],
				pass: [{ required: true, validator: validatePass, trigger: 'blur' }]
			}
		};
	},
 
	methods: {
		//选项卡切换
		handleClick(tab, event) {},
		//重置表单
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		//提交表单
		submitForm(formName) {
			if(this.ruleForm.name=='user123'&&this.ruleForm.pass=='12345'){
				alert("登陆成功!");
				this.$router.push("/AboutUs");
			}else{
				alert("账号或密码错误！")
			}

		}
	},
	components: {
		RegisterCom
	}
};
</script>
 
